import React, { useEffect, memo } from 'react';
import Title from "./Title.js";
import "./ProjectOverlay.css";
import ReactMarkdown from 'react-markdown';
import { ReactComponent as CloseIcon } from "../images/cross.svg";

function ProjectOverlay({ close, project }) {

    useEffect(() => {
        document.documentElement.style.setProperty('--blur-amount', 2);

        return () => {
            document.documentElement.style.setProperty('--blur-amount', 0);
        }
    }, [])

    const fillColor = document.documentElement.getAttribute("data-theme") === "light" ? "fill: #eaeaea" : "#222"
    return (
        <div className='grid lg:grid-cols-4 project-overlay show'>
            <div className='project-blur hidden lg:flex' onClick={() => close()}>
            </div>
            <div className="project-info col-span-3 shadow-xl">
                <div className='flex justify-end sm:hidden'>
                    <button onClick={() => close()}>
                        <CloseIcon style={{ margin: 20, }} className="close-svg" />
                    </button>
                </div>
                <div>
                    <div className='flex sm:justify-between items-center m-4 sm:m-8'>
                        <Title title={project.title} />
                        <button className='hidden sm:flex' onClick={() => close()}>
                            <CloseIcon style={{ margin: 20 }} />
                        </button>
                    </div>
                    {/* Content */}
                    <div className='grid grid-cols-1 sm:grid-cols-3 gap-16 mx-4 sm:mx-8'>
                        <p className='sm:col-span-2'>
                            <ReactMarkdown>{project.description}</ReactMarkdown>
                            Lorem ipsum dolor sit amet, consectetur adipiscing elit. Morbi metus orci, dictum non dolor mollis, commodo vehicula lectus. Integer feugiat nisl nulla, dictum tincidunt nunc molestie accumsan. Sed consectetur dignissim augue vitae molestie. Vestibulum justo diam, convallis in pharetra quis, hendrerit id nulla. Pellentesque fermentum iaculis enim, ac sodales ipsum vulputate a. Duis consequat interdum eleifend. Curabitur lacus tellus, venenatis vitae lorem in, blandit tristique turpis. Orci varius natoque penatibus et magnis dis parturient montes, nascetur ridiculus mus. Vestibulum ante ipsum primis in faucibus orci luctus et ultrices posuere cubilia curae; Suspendisse ultricies tempus ex id tincidunt. Vivamus sit amet turpis eu nibh gravida commodo eu non velit. Integer posuere fermentum dui sit amet varius. Nulla molestie congue nulla, nec facilisis metus blandit non. Nulla id arcu congue, lacinia massa eu, mattis velit. Donec et aliquet augue, nec scelerisque eros. Vestibulum neque elit, consequat vitae posuere sit amet, maximus ut dui. Mauris porta convallis faucibus. In lobortis tortor sed orci consequat, vel tempus elit auctor. Donec porta pulvinar tempus. Ut interdum lacus et tempus ullamcorper. In enim massa, aliquam eu nunc sed, rhoncus dignissim augue. Aenean rhoncus in ligula a luctus. Proin eu lorem sed nulla rhoncus facilisis. Fusce eget risus at neque luctus placerat. Cras a ullamcorper eros. Aenean venenatis sem a massa tempus, id vehicula elit pharetra. Sed ultrices sodales mi, fringilla venenatis diam pulvinar vitae. Proin tristique tellus ex, quis condimentum sapien dignissim et.
                        </p>
                        {/* Meta data */}
                        <div className='flex flex-col'>
                            {
                                project.metaData !== undefined ? project.metaData.map((meta, i) => <ReactMarkdown key={i}>{meta}</ReactMarkdown>
                                ) : <></>
                            }
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default memo(ProjectOverlay);